@import "./../../sass/mixins";
@import "./../../sass/vars";

.about {
  min-height: 100vh;
  @include flexCenter;
  position: relative;
  @include pseudoTag('<section>', '</section>', -0.8, -1, -1.5);
  &:before {
    left: 1rem;
  }
  &:after {
    right: 4rem;
  }
  .about_inner {
    display: flex;
    justify-content: space-between;
    align-items: center;
    flex-wrap: wrap;
  }
}
.descr_wrap {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  padding: 5rem 0;
   p + p {
     padding: 10px 0;

   }
  @include pseudoTag('<p>', '</p>', -0.8, -1, -0.5);
  &:after {
    right: 2rem;
  }
}

.about_column {
  max-width: 50%;
  @media (max-width: 1100px) {
  max-width: none;
  }
}
.about_descr {
  line-height: 1.7;
  font-size: 1.6rem;
  @media (max-width: $mq3 + px) {

  }
}
